import { useDispatch } from "react-redux";
import { userSlice } from "../../redux/User";
import { axiosGet } from "../../services/axiosService";
import * as FacebookService from "@/services/facebook.service";

function FacebookBtn() {
  const dispatch = useDispatch();

  async function handleFacebookLogin() {
    try {
      const response = await axiosGet("/auth");
      const { codeChallenge } = response.data.data;
      if (!codeChallenge) throw new Error("Login canceled");

      console.log(codeChallenge, "codeChallenge", window.location.origin);
      FacebookService.userLogin(codeChallenge);
    } catch (err: any) {
      dispatch(
        userSlice.actions.setInfoModal({
          showModal: true,
          modalType: "error",
          modalTitle: "Oops!",
          modalMessage:
            err.response?.data?.message ??
            err?.message ??
            "Something went wrong",
        })
      );
    }
  }

  return (
    <div className="social">
      <button
        className="buttonSocial"
        id="facebook"
        type="button"
        onClick={handleFacebookLogin}
      >
        <div className="text-wrapper">
          <svg
            width="18"
            height="18"
            viewBox="0 0 18 18"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <g clipPath="url(#clip0_6503_9214)">
              <path
                d="M18 9C18 13.4923 14.7087 17.2157 10.4062 17.8907V11.6016H12.5033L12.9023 9H10.4062V7.3118C10.4062 6.59988 10.755 5.90625 11.873 5.90625H13.0078V3.69141C13.0078 3.69141 11.9777 3.51562 10.993 3.51562C8.93742 3.51562 7.59375 4.76156 7.59375 7.01719V9H5.30859V11.6016H7.59375V17.8907C3.29133 17.2157 0 13.4923 0 9C0 4.02961 4.02961 0 9 0C13.9704 0 18 4.02961 18 9Z"
                fill="#1877F2"
              />
              <path
                d="M12.5072 11.6016L12.9062 9H10.4102V7.31176C10.4102 6.60002 10.7588 5.90625 11.8768 5.90625H13.0117V3.69141C13.0117 3.69141 11.9818 3.51562 10.9971 3.51562C8.94129 3.51562 7.59766 4.76156 7.59766 7.01719V9H5.3125V11.6016H7.59766V17.8906C8.05588 17.9625 8.5255 18 9.00391 18C9.48231 18 9.95193 17.9625 10.4102 17.8906V11.6016H12.5072Z"
                fill="white"
              />
            </g>
            <defs>
              <clipPath id="clip0_6503_9214">
                <rect width="18" height="18" fill="white" />
              </clipPath>
            </defs>
          </svg>
          <span className="social-btn-text">Continue with Facebook</span>
        </div>
      </button>
    </div>
  );
}

export default FacebookBtn;
