import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
function FormInput({
  labelName = "demo",
  placeHolder = "",
  inputType = "text",
  isRequired = true,
  masterDivClassName = "form-group",
  span = "text",
  inputClassName = "form-control",
  value = "",
  onDataChange = () => {},
  onBlur = () => {},
  page = "",
  emailRef,
  passwordRef,
  onKeyDown,
}: {
  value?: string;
  labelName?: string;
  placeHolder?: string;
  span?: string;
  inputType?: string;
  isRequired?: boolean;
  masterDivClassName?: string;
  inputClassName?: string;
  onDataChange?: (data: any) => void;
  onBlur?: () => void;
  page?: string;
  emailRef?: any;
  passwordRef?: any;
  onKeyDown?: any;
}) {
  const [showPassword, setShowPassword] = useState(false);
  const [passInput, setPassInput] = useState("password");
  const [validationPopup, setvalidationPopup] = useState("none");
  const [lowerCase, setLowerCase] = useState<any>(false);
  const [upperCase, setUpperCase] = useState<any>(false);
  const [number, setNumber] = useState<any>(false);
  const [lengthCharacter, setlengthCharacter] = useState<any>(false);
  const [length, setLength] = useState<any>(false);
  const handleShowPassword = () => {
    setShowPassword((prev) => !prev);
    setPassInput((prev) => {
      if (prev === "text") return "password";
      else return "text";
    });
  };
  const handlePasswordValid = (e) => {
    const upperCase = new RegExp(/[A-Z]/g);
    upperCase.test(e.target.value) ? setUpperCase(true) : setUpperCase(false);

    const lowerCase = new RegExp(/[a-z]/g);
    lowerCase.test(e.target.value) ? setLowerCase(true) : setLowerCase(false);

    const number = new RegExp(/[0-9]/);
    number.test(e.target.value) ? setNumber(true) : setNumber(false);

    const character = new RegExp(/[!@#$%^&*_\-~`=+'\[\]";:\|?,<.>]/);
    character.test(e.target.value) ? setlengthCharacter(true) : setlengthCharacter(false);

    e.target.value.length >= 8 ? setLength(true) : setLength(false);
  };
  const validSVG = () => {
    return (
      <svg width="10" height="8" viewBox="0 0 10 8" fill="none" xmlns="http://www.w3.org/2000/svg">
        <path
          d="M3.58306 5.52636L2.04107 3.92431L2.04104 3.92429C1.74572 3.61756 1.26291 3.61756 0.967594 3.92429L0.967539 3.92434C0.677487 4.22579 0.677487 4.71062 0.967539 5.01206L0.967599 5.01212L2.79749 6.91257C2.79752 6.91261 2.79756 6.91265 2.7976 6.91269L2.79766 6.91276L2.97775 6.73936C3.13819 6.90632 3.35598 7.0001 3.58306 7L3.58295 7.25C3.87961 7.25013 4.16208 7.12753 4.36864 6.91257L9.03242 2.06786L9.03246 2.06782C9.32251 1.76637 9.32251 1.28154 9.03246 0.980099L9.03241 0.980042C8.73709 0.673319 8.25428 0.67332 7.95896 0.980042L7.95893 0.98007L3.58306 5.52636Z"
          fill="#00FF00"
          stroke="#00FF00"
          strokeWidth="0.5"
        />
      </svg>
    );
  };
  const invalidSVG = () => {
    return (
      <svg width="6" height="6" viewBox="0 0 6 6" fill="none" xmlns="http://www.w3.org/2000/svg">
        <circle cx="3" cy="3" r="3" fill="black" fillOpacity="0.5" />
      </svg>
    );
  };
  const router = useNavigate();
  const handleForgotPassword = () => {
    router("/forgotpassword");
  };

  const showValidationModal = () => {
    setvalidationPopup("");
  };

  const hideValidationModal = () => {
    setvalidationPopup("none");
  };
  return (
    <>
      {inputType === "checkbox" ? (
        <>
          <div className={`${masterDivClassName}`}>
            <label htmlFor="">{labelName}</label>
            <input type={`${inputType}`} className={`${inputClassName}`} />
          </div>
        </>
      ) : inputType === "text" ? (
        <>
          <div className="email-input">
            <label htmlFor="">{labelName}</label>
            <div className="em-input-bx">
              <div className="icn-bx-mail">
                {labelName === 'Promo Code' ? (
                      <svg width="16" height="12" viewBox="0 0 16 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                      <path
                        d="M14.4999 11.8675H1.5C0.672863 11.8675 0 11.2079 0 10.3972V7.94678C0 7.67589 0.223625 7.4567 0.500001 7.4567C1.32714 7.4567 2.00001 6.79718 2.00001 5.98644C2.00001 5.17571 1.32714 4.51619 0.500001 4.51619C0.223625 4.51623 0 4.29704 0 4.02614V1.57572C0 0.764985 0.672863 0.105469 1.5 0.105469H14.5C15.3271 0.105469 16 0.764985 16 1.57572V4.02614C16 4.29704 15.7764 4.51623 15.5 4.51623C14.6728 4.51623 14 5.17574 14 5.98648C14 6.79721 14.6728 7.45673 15.5 7.45673C15.7764 7.45673 16 7.67592 16 7.94681V10.3972C15.9999 11.2079 15.3271 11.8675 14.4999 11.8675ZM1 8.38757V10.3972C1 10.6676 1.22413 10.8873 1.5 10.8873H14.5C14.7758 10.8873 15 10.6676 15 10.3972V8.38757C13.8603 8.15977 13 7.17003 13 5.98644C13 4.80286 13.8603 3.81315 15 3.58532V1.57572C15 1.30532 14.7758 1.08564 14.5 1.08564H1.5C1.22413 1.08564 1 1.30532 1 1.57572V3.58535C2.13964 3.81316 3.00001 4.80289 3.00001 5.98648C3.00001 7.17006 2.13964 8.15977 1 8.38757Z"
                        fill="#B3B3B3"
                      />
                      <path
                        d="M6.03031 9.72913L9.02921 1.89062L9.96552 2.23477L6.96663 10.0733L6.03031 9.72913ZM5.63282 6.15227C4.80568 6.15227 4.13281 5.49276 4.13281 4.68202C4.13281 3.87129 4.80568 3.21177 5.63282 3.21177C6.45996 3.21177 7.13282 3.87129 7.13282 4.68202C7.13282 5.49276 6.45996 6.15227 5.63282 6.15227ZM5.63282 4.19194C5.35695 4.19194 5.13282 4.41162 5.13282 4.68202C5.13282 4.95242 5.35695 5.17211 5.63282 5.17211C5.90869 5.17211 6.13282 4.95242 6.13282 4.68202C6.13282 4.41162 5.90869 4.19194 5.63282 4.19194ZM10.8011 9.19173C9.97397 9.19173 9.30111 8.53222 9.30111 7.72148C9.30111 6.91074 9.97397 6.25123 10.8011 6.25123C11.6283 6.25123 12.3011 6.91074 12.3011 7.72148C12.3011 8.53222 11.6283 9.19173 10.8011 9.19173ZM10.8011 7.2314C10.5252 7.2314 10.3011 7.45108 10.3011 7.72148C10.3011 7.99188 10.5252 8.21157 10.8011 8.21157C11.077 8.21157 11.3011 7.99188 11.3011 7.72148C11.3011 7.45108 11.077 7.2314 10.8011 7.2314Z"
                        fill="#B3B3B3"
                      />
                    </svg>
                ) : (
                  <svg width="13" height="13" viewBox="0 0 13 13" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <path
                    d="M10.2917 0.541687H2.70833C1.9903 0.542547 1.30193 0.828165 0.794202 1.33589C0.286478 1.84361 0.00086009 2.53199 0 3.25002L0 9.75002C0.00086009 10.4681 0.286478 11.1564 0.794202 11.6642C1.30193 12.1719 1.9903 12.4575 2.70833 12.4584H10.2917C11.0097 12.4575 11.6981 12.1719 12.2058 11.6642C12.7135 11.1564 12.9991 10.4681 13 9.75002V3.25002C12.9991 2.53199 12.7135 1.84361 12.2058 1.33589C11.6981 0.828165 11.0097 0.542547 10.2917 0.541687ZM2.70833 1.62502H10.2917C10.616 1.62566 10.9327 1.72334 11.2011 1.9055C11.4695 2.08765 11.6772 2.34595 11.7975 2.64715L7.64942 6.79577C7.34415 7.09982 6.93085 7.27052 6.5 7.27052C6.06915 7.27052 5.65585 7.09982 5.35058 6.79577L1.2025 2.64715C1.32283 2.34595 1.53054 2.08765 1.7989 1.9055C2.06726 1.72334 2.38399 1.62566 2.70833 1.62502ZM10.2917 11.375H2.70833C2.27736 11.375 1.86403 11.2038 1.55928 10.8991C1.25454 10.5943 1.08333 10.181 1.08333 9.75002V4.06252L4.58467 7.56169C5.09309 8.06883 5.78189 8.35362 6.5 8.35362C7.21811 8.35362 7.90691 8.06883 8.41533 7.56169L11.9167 4.06252V9.75002C11.9167 10.181 11.7455 10.5943 11.4407 10.8991C11.136 11.2038 10.7226 11.375 10.2917 11.375Z"
                    fill="black"
                    fillOpacity="0.3"
                  />
                </svg>
                )}
                
              </div>
              <input
                ref={emailRef}
                type={`${inputType}`}
                className={`${inputClassName}`}
                onChange={(e) => onDataChange(e)}
                value={value}
                onBlur={onBlur}
                // autoFocus={true}
                placeholder={placeHolder}
                onKeyDown={(event) => event.key === "Enter" && onKeyDown && onKeyDown()}
              />
            </div>
          </div>
        </>
      ) : inputType === "password" ? (
        <>
          <div className="email-input">
            <label htmlFor="">{labelName}</label>
            <div className="em-input-bx">
              <div className="icn-bx-mail">
                <svg width="13" height="13" viewBox="0 0 13 13" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <g clipPath="url(#clip0_4244_961)">
                    <path
                      d="M10.2918 4.563V3.79167C10.2918 2.78605 9.89235 1.82163 9.18128 1.11055C8.4702 0.399478 7.50578 0 6.50016 0C5.49455 0 4.53013 0.399478 3.81905 1.11055C3.10797 1.82163 2.7085 2.78605 2.7085 3.79167V4.563C2.22607 4.77355 1.81545 5.12012 1.52685 5.56032C1.23825 6.00053 1.08419 6.51529 1.0835 7.04167V10.2917C1.08436 11.0097 1.36997 11.6981 1.8777 12.2058C2.38542 12.7135 3.0738 12.9991 3.79183 13H9.2085C9.92653 12.9991 10.6149 12.7135 11.1226 12.2058C11.6304 11.6981 11.916 11.0097 11.9168 10.2917V7.04167C11.9161 6.51529 11.7621 6.00053 11.4735 5.56032C11.1849 5.12012 10.7743 4.77355 10.2918 4.563ZM3.79183 3.79167C3.79183 3.07337 4.07717 2.3845 4.58508 1.87659C5.09299 1.36867 5.78187 1.08333 6.50016 1.08333C7.21846 1.08333 7.90733 1.36867 8.41524 1.87659C8.92315 2.3845 9.2085 3.07337 9.2085 3.79167V4.33333H3.79183V3.79167ZM10.8335 10.2917C10.8335 10.7226 10.6623 11.136 10.3575 11.4407C10.0528 11.7455 9.63947 11.9167 9.2085 11.9167H3.79183C3.36085 11.9167 2.94753 11.7455 2.64278 11.4407C2.33803 11.136 2.16683 10.7226 2.16683 10.2917V7.04167C2.16683 6.61069 2.33803 6.19736 2.64278 5.89262C2.94753 5.58787 3.36085 5.41667 3.79183 5.41667H9.2085C9.63947 5.41667 10.0528 5.58787 10.3575 5.89262C10.6623 6.19736 10.8335 6.61069 10.8335 7.04167V10.2917Z"
                      fill="black"
                      fillOpacity="0.3"
                    />
                    <path
                      d="M6.50016 7.58334C6.3565 7.58334 6.21873 7.64041 6.11715 7.74199C6.01556 7.84358 5.9585 7.98135 5.9585 8.12501V9.20834C5.9585 9.352 6.01556 9.48978 6.11715 9.59136C6.21873 9.69294 6.3565 9.75001 6.50016 9.75001C6.64382 9.75001 6.7816 9.69294 6.88318 9.59136C6.98476 9.48978 7.04183 9.352 7.04183 9.20834V8.12501C7.04183 7.98135 6.98476 7.84358 6.88318 7.74199C6.7816 7.64041 6.64382 7.58334 6.50016 7.58334Z"
                      fill="black"
                      fillOpacity="0.3"
                    />
                  </g>
                  <defs>
                    <clipPath id="clip0_4244_961">
                      <rect width="13" height="13" fill="white" />
                    </clipPath>
                  </defs>
                </svg>
              </div>
              <input
                // autoComplete="new-password"
                ref={passwordRef}
                type={passInput}
                className={`${inputClassName}`}
                onChange={(e) => {
                  onDataChange(e);
                  handlePasswordValid(e);
                }}
                placeholder={placeHolder}
                onFocus={showValidationModal}
                onBlur={() => {
                  onBlur();
                  hideValidationModal();
                }}
                onKeyDown={(event) => event.key === "Enter" && onKeyDown && onKeyDown()}
              />
              {!showPassword ? (
                <svg
                  className="password-off"
                  width="16"
                  height="16"
                  viewBox="0 0 16 16"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                  onClick={handleShowPassword}
                >
                  <path
                    d="M2.61979 11.255L3.33479 10.545C2.5519 9.84206 1.93677 8.97235 1.53479 8C2.54979 5.465 5.34979 3.5 7.99979 3.5C8.68173 3.509 9.35751 3.63064 9.99979 3.86L10.7748 3.08C9.89611 2.70866 8.95363 2.51167 7.99979 2.5C6.37005 2.56129 4.79398 3.09878 3.46639 4.04604C2.1388 4.99331 1.11787 6.30882 0.529787 7.83C0.490071 7.93985 0.490071 8.06015 0.529787 8.17C0.973911 9.34851 1.69 10.4055 2.61979 11.255V11.255Z"
                    fill="black"
                    fillOpacity="0.3"
                  />
                  <path
                    d="M6 7.865C6.03477 7.38586 6.24084 6.93524 6.58054 6.59554C6.92024 6.25584 7.37086 6.04977 7.85 6.015L8.755 5.105C8.24788 4.97147 7.71462 4.97321 7.20838 5.11003C6.70214 5.24685 6.24061 5.51399 5.8698 5.8848C5.49899 6.25561 5.23185 6.71714 5.09503 7.22338C4.95821 7.72962 4.95647 8.26288 5.09 8.77L6 7.865ZM15.47 7.83C14.8967 6.3366 13.899 5.04351 12.6 4.11L15 1.705L14.295 1L1 14.295L1.705 15L4.255 12.45C5.39194 13.117 6.68202 13.4787 8 13.5C9.62974 13.4387 11.2058 12.9012 12.5334 11.954C13.861 11.0067 14.8819 9.69118 15.47 8.17C15.5097 8.06015 15.5097 7.93985 15.47 7.83V7.83ZM10 8C9.99789 8.35005 9.90395 8.69342 9.72755 8.99578C9.55116 9.29815 9.29849 9.54892 8.9948 9.72303C8.69112 9.89714 8.34705 9.9885 7.997 9.98797C7.64694 9.98744 7.30315 9.89504 7 9.72L9.72 7C9.89975 7.30287 9.99634 7.64783 10 8V8ZM8 12.5C6.95103 12.4817 5.92195 12.2107 5 11.71L6.27 10.44C6.84767 10.8408 7.54777 11.026 8.24807 10.9632C8.94836 10.9005 9.60436 10.5937 10.1015 10.0965C10.5987 9.59936 10.9055 8.94336 10.9682 8.24307C11.031 7.54277 10.8458 6.84267 10.445 6.265L11.88 4.83C13.0273 5.61749 13.9245 6.71771 14.465 8C13.45 10.535 10.65 12.5 8 12.5Z"
                    fill="black"
                    fillOpacity="0.3"
                  />
                </svg>
              ) : (
                <svg
                  className="password-on"
                  width="16"
                  height="16"
                  viewBox="0 0 16 16"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                  onClick={handleShowPassword}
                >
                  <path
                    d="M8 2.5C13 2.5 16 8 16 8C16 8 13 13.5 8 13.5C3 13.5 0 8 0 8C0 8 3 2.5 8 2.5ZM8 3.5C4.48 3.5 1.998 6.751 1.175 8C1.997 9.248 4.479 12.5 8 12.5C11.52 12.5 14.002 9.249 14.825 8C14.003 6.752 11.521 3.5 8 3.5V3.5ZM8 4.5C8.92826 4.5 9.8185 4.86875 10.4749 5.52513C11.1313 6.1815 11.5 7.07174 11.5 8C11.5 8.92826 11.1313 9.8185 10.4749 10.4749C9.8185 11.1313 8.92826 11.5 8 11.5C7.07174 11.5 6.1815 11.1313 5.52513 10.4749C4.86875 9.8185 4.5 8.92826 4.5 8C4.5 7.07174 4.86875 6.1815 5.52513 5.52513C6.1815 4.86875 7.07174 4.5 8 4.5V4.5ZM8 5.5C7.3372 5.50079 6.70178 5.76444 6.23311 6.23311C5.76444 6.70178 5.50079 7.3372 5.5 8C5.5 9.378 6.621 10.5 8 10.5C9.379 10.5 10.5 9.378 10.5 8C10.5 6.622 9.379 5.5 8 5.5Z"
                    fill="black"
                    fillOpacity="0.3"
                  />
                </svg>
              )}
            </div>

            {page && page == "signup" && !(lowerCase && upperCase && number && lengthCharacter && length) ? (
              <div className={"password-validation " + validationPopup}>
                <ul className="validation-ul">
                  <li>
                    {lowerCase ? validSVG() : invalidSVG()}
                    <span className={lowerCase ? "valid" : "invalid"}>1 Lowercase Character</span>
                  </li>
                  <li>
                    {upperCase ? validSVG() : invalidSVG()}
                    <span className={upperCase ? "valid" : "invalid"}>1 UpperCase Character</span>
                  </li>
                  <li>
                    {number ? validSVG() : invalidSVG()}
                    <span className={number ? "valid" : "invalid"}>1 Numerical Character</span>
                  </li>
                  <li>
                    {lengthCharacter ? validSVG() : invalidSVG()}
                    <span className={lengthCharacter ? "valid" : "invalid"}>1 special Character</span>
                  </li>
                  <li>
                    {length ? validSVG() : invalidSVG()}
                    <span className={length ? "valid" : "invalid"}>A Minimum Of 8 Characters</span>
                  </li>
                </ul>
              </div>
            ) : null}

            <div className="fg-link">
              <span onClick={handleForgotPassword}>{span}</span>
            </div>
          </div>
        </>
      ) : inputType === "radio" ? (
        <></>
      ) : (
        <>
          <div className={`${masterDivClassName}`}>
            <label htmlFor="">{labelName}</label>
            <input type={`${inputType}`} className={`${inputClassName}`} required={isRequired} />
          </div>
        </>
      )}
    </>
  );
}

export default FormInput;
