import { useDispatch } from "react-redux";
import { userSlice } from "../../redux/User";
import { axiosGet } from "../../services/axiosService";
import environment from "../../constants/Environment";

function GoogleBtn() {
  const dispatch = useDispatch();
  const environmentData = environment();

  async function handleGoogleLogin() {
    try {
      const response = await axiosGet("/auth");
      const { codeChallenge } = response.data.data;
      if (!codeChallenge) throw new Error("Login canceled");

      const query = new URLSearchParams({
        client_id: environmentData.PUBLIC_GOOGLE_APP_ID,
        redirect_uri: window.location.origin + "/auth/google",
        response_type: "code",
        prompt: "consent",
        scope: ["openid", "email", "profile"].join(" "),
        code_challenge: codeChallenge,
        code_challenge_method: "S256",
      });

      window.open(`https://accounts.google.com/o/oauth2/v2/auth?${query}`, "_top");
    } catch (err: any) {
      dispatch(
        userSlice.actions.setInfoModal({
          showModal: true,
          modalType: "error",
          modalTitle: "Oops!",
          modalMessage: err.response?.data?.message ?? err?.message ?? "Something went wrong",
        })
      );
    }
  }

  return (
    <div className="social">
      <button className="buttonSocial" id="google-btn" type="button" onClick={handleGoogleLogin}>
        <div className="text-wrapper">
          <svg width="18" height="18" viewBox="0 0 18 18" fill="none" xmlns="http://www.w3.org/2000/svg">
            <g clipPath="url(#clip0_6503_9206)">
              <path
                d="M3.98918 10.878L3.36263 13.217L1.07258 13.2654C0.388195 11.996 0 10.5437 0 9.00034C0 7.50793 0.362953 6.10055 1.00631 4.86133H1.0068L3.04559 5.23511L3.9387 7.26166C3.75177 7.80661 3.64989 8.39161 3.64989 9.00034C3.64996 9.661 3.76963 10.294 3.98918 10.878Z"
                fill="#FBBB00"
              />
              <path
                d="M17.8402 7.31836C17.9436 7.86279 17.9975 8.42504 17.9975 8.99967C17.9975 9.64402 17.9297 10.2725 17.8006 10.8788C17.3625 12.9419 16.2177 14.7434 14.6319 16.0182L14.6314 16.0177L12.0634 15.8867L11.7 13.6179C12.7523 13.0007 13.5747 12.035 14.0079 10.8788H9.19531V7.31836H14.0781H17.8402Z"
                fill="#518EF8"
              />
              <path
                d="M14.6316 16.0183L14.6321 16.0188C13.0898 17.2585 11.1305 18.0003 8.99773 18.0003C5.57031 18.0003 2.59043 16.0846 1.07031 13.2654L3.98691 10.8779C4.74695 12.9064 6.70372 14.3503 8.99773 14.3503C9.98376 14.3503 10.9075 14.0838 11.7002 13.6185L14.6316 16.0183Z"
                fill="#28B446"
              />
              <path
                d="M14.7462 2.07197L11.8306 4.45894C11.0102 3.94615 10.0404 3.64992 9.0015 3.64992C6.65556 3.64992 4.6622 5.16013 3.94023 7.26131L1.0083 4.86098H1.00781C2.50568 1.97307 5.52314 0 9.0015 0C11.1852 0 13.1875 0.777867 14.7462 2.07197Z"
                fill="#F14336"
              />
            </g>
            <defs>
              <clipPath id="clip0_6503_9206">
                <rect width="18" height="18" fill="white" />
              </clipPath>
            </defs>
          </svg>
          <span className="social-btn-text">Continue with Google</span>
        </div>
      </button>
    </div>
  );
}

export default GoogleBtn;
